import Vue from 'vue'
import Router from 'vue-router'

import landingLayout from './views/landing/layout'
import authPanel from './views/auth/index'
import loginPanel from './views/auth/login'
import resetPanel from './views/auth/reset'
import panelLayout from './views/dashboard/layout'
import workerView from './views/dashboard/workers'
import clientsView from './views/dashboard/clients'
import aboutPage from './views/landing/about/index'
import roomsPage from './views/landing/rooms/index'
import contactPage from './views/landing/contact/index'
import newsPage from './views/landing/news/index'

Vue.use(Router)

export default new Router({
  routes: [{
      path: '/auth',
      redirect: '/auth/login',
      component: authPanel,
      children: [{
          path: '/auth/login',
          name: 'Login',
          component: loginPanel
        },
        {
          path: '/auth/reset',
          name: 'Reset',
          component: resetPanel
        },
      ]
    },
    {
      path: '/panel',
      name: 'Panel',
      component: panelLayout,
      children: [{
          path: '/panel/workers',
          name: 'Workers',
          component: workerView
        },
        {
          path: '/panel/clients',
          name: 'Clients',
          component: clientsView
        }
      ]
    },
    {
      path: '/',
      name: 'Home',
      component: landingLayout,
      redirect: '/about',
      children: [{
          path: '/about',
          name: 'About',
          component: aboutPage
        },
        {
          path: '/rooms',
          name: 'Rooms',
          component: roomsPage
        },
        {
          path: '/contact',
          name: 'Contact',
          component: contactPage
        },
        {
          path: '/news',
          name: 'News',
          component: newsPage
        }
      ]
    },
  ],
  mode: 'history'
})

/*
// route level code-splitting
// this generates a separate chunk (about.[hash].js) for this route
// which is lazy-loaded when the route is visited.
component: () => import(/* webpackChunkName: "about"  './views/About.vue')
*/