<template>
    <div class="login-panel">
        <router-link to="/">
            <img src="@/assets/img/logo-hotel.png" alt="logo">
        </router-link>
        <h3>Logowanie</h3>
        <md-field md-theme="default-gold" class="panel-input">
            <label>E-mail</label>
            <md-input v-model="user.email"></md-input>
        </md-field>
        <md-field md-theme="default-gold" class="panel-input">
            <label>Hasło</label>
            <md-input v-model="user.password" type="password"></md-input>
        </md-field>
        <div class="submit-box">
            <router-link to="/auth/reset">
                Zapomniałeś hasła?
            </router-link>
            <button @click="checkAuth">
                Zaloguj się
            </button>
        </div>
    </div>
</template>
<script>
import axios from 'axios';
axios.defaults.baseURL = 'https://hotel-back.cleverapps.io';
// axios.defaults.baseURL = 'http://localhost:3000/';
export default {
    data() {
        return {
            user: {
                email: '',
                password: ''
            }
        }
    },
    methods: {
        checkAuth() {
            axios.post('/login-user', {
                user: this.user
            })
            .then(res => {
                if (res.data.isUserLogged === true) {
                    this.$router.push('/panel/workers');
                }
            })
        }
    }
}
</script>
