<template>
    <div class="workers-component">
        <h1 class="main-header">Pracownicy</h1>
        <div class="cards-row">
            <div class="card-2">
                <h1 class="card-title">Tworzenie pracowników</h1>
                <div class="card-content">
                    <form class="add-user-form" @submit.prevent="saveWorker">
                        <div class="form-cols">
                            <div class="form-col">
                                <md-field>
                                    <label>Imie!</label>
                                    <md-input required v-model="user.firstName"></md-input>
                                </md-field>
                                <md-field>
                                    <label>Nazwisko</label>
                                    <md-input required v-model="user.lastName"></md-input>
                                </md-field>
                                <md-field>
                                    <label>E-mail!</label>
                                    <md-input required type="email" v-model="user.email"></md-input>
                                </md-field>
                                <md-field v-if="user.role == 'worker'">
                                    <label for="department">Dział</label>
                                    <md-select required v-model="user.department" name="department" id="department">
                                        <md-option value="management">Administracja</md-option>
                                        <md-option value="restaurant">Gastronomia</md-option>
                                        <md-option value="wellness">Wellness & SPA</md-option>
                                        <md-option value="service">Dział usługowo-sprzątający</md-option>
                                    </md-select>
                                </md-field>
                            </div>
                            <div class="form-col">
                                <md-field>
                                    <label>PESEL</label>
                                    <md-input required maxlength="11" v-model="user.pesel"></md-input>
                                </md-field>
                                <md-field>
                                    <label>Hasło</label>
                                    <md-input required v-model="user.password"></md-input>
                                </md-field>
                                <md-field>
                                    <label for="role">Uprawnienia</label>
                                    <md-select required v-model="user.role" name="role" id="role">
                                        <md-option value="admin">Administrator</md-option>
                                        <md-option value="worker">Pracownik</md-option>
                                    </md-select>
                                </md-field>
                            </div>
                        </div>
                        <button type="submit">Utwórz pracownika</button>
                    </form>
                </div>
            </div>
            <div class="card-1">
                <h1 class="card-title">Ilość pracowników w działach</h1>
                <div class="card-content">
                    <div class="department-box green">
                        <p>Administracja</p>
                        <div class="amount">
                            <span>{{managementAmount}}</span>
                            <img src="@/assets/img/worker.png" alt="person icon">
                        </div>
                    </div>
                    <div class="department-box brown">
                        <p>Gastronomia</p>
                        <div class="amount">
                            <span>{{restaurantAmount}}</span>
                            <img src="@/assets/img/worker.png" alt="person icon">
                        </div>
                    </div>
                    <div class="department-box blue ">
                        <p>Wellness & SPA</p>
                        <div class="amount">
                            <span>{{wellnessAmount}}</span>
                            <img src="@/assets/img/worker.png" alt="person icon">
                        </div>
                    </div>
                    <div class="department-box dark">
                        <p>Dział usługowo-sprzątający</p>
                        <div class="amount">
                            <span>{{serviceAmount}}</span>
                            <img src="@/assets/img/worker.png" alt="person icon">
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="cards-row">
            <div class="card-3">
                <h1 class="card-title">Lista użytkowników</h1>
                <div class="card-content">
                    <div class="users-list">
                        <div class="list-header">
                            <p class="user-name">Imie i nazwisko</p>
                            <p class="user-email">E-mail</p>
                            <p class="user-pesel">PESEL</p>
                            <p class="user-department">Dział</p>
                        </div>
                        <div v-for="user in users" :class="['user-row', user.department == 'Administracja' ? 'green' : '', user.department == 'Restauracja' ? 'brown' : '', user.department == 'Wellness & SPA' ? 'blue' : '', user.department == 'Dział usługowo - sprzątający' ? 'dark' : '']" :key="user._id">
                            <p class="user-name">{{user.firstName + ' ' + user.lastName}}</p>
                            <p class="user-email">{{user.email}}</p>
                            <p class="user-pesel">{{user.pesel}}</p>
                            <p class="user-department">{{user.department}}</p>
                            <div class="user-delete">
                                <button @click="deleteUser(user._id)">Usuń</button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import axios from 'axios';
axios.defaults.baseURL = 'https://hotel-back.cleverapps.io';
// axios.defaults.baseURL = 'http://localhost:3000';
    export default {
        data() {
            return {
                user: {
                    firstName: '',
                    lastName: '',
                    email: '',
                    password: '',
                    pesel: '',
                    role: '',
                    department: 'all'
                },
                managementAmount: 0,
                restaurantAmount: 0,
                wellnessAmount: 0,
                serviceAmount: 0,
                users: []
            }
        },
        beforeMount() {
            this.getWorkers();
        },
        methods: {
            getWorkers() {
                axios.get('/get-workers')
                    .then(res => {
                        const users = res.data.users;
                        for (let i in users) {
                            if (users[i].department == 'management') {
                                users[i].department = "Administracja"
                                this.managementAmount += 1;
                            } else if (users[i].department == 'wellness') {
                                users[i].department = "Wellness & SPA";
                                this.wellnessAmount += 1;
                            } else if (users[i].department == 'restaurant') {
                                users[i].department = "Restauracja"
                                this.restaurantAmount += 1;
                            } else if (users[i].department == 'service') {
                                users[i].department = "Dział usługowo - sprzątający";
                                this.serviceAmount +=1 ;
                            }
                        }
                        this.users = users;
                    })
            },
            deleteUser(id) {
                axios.post('/delete-worker', {
                        id: id
                    })
                    .then(res => {
                        /* eslint-disable */
                        console.log(res.data);
                        this.$router.go();
                    })
            },
            saveWorker() {
                axios.post('/add-worker', {
                        user: this.user
                    })
                    /* eslint-disable */
                    .then(res => {
                        this.$router.go();
                    })
            }
        }
    }
</script>